.toggle_label {
  position: relative;
  display: inline-block;
  width: 49px;
  height: 28px;
  transition: 0.3s ease-in-out;
  z-index: 2;
}
.toggle_label input {
  opacity: 0;
  width: 0;
  height: 0;
  transition: 0.3s ease-in-out;
}
.toggle_label input:checked + .button_slider {
  background-color: var(--ion-color-primary);
}
.toggle_label input:checked + .button_slider .button_switch_round {
  transform: translateX(21px);
}
.toggle_label input:checked + .button_slider .button_switch_round #theme_svg_icon #sun {
  display: none;
}
.toggle_label input:checked + .button_slider .button_switch_round #theme_svg_icon #moon {
  display: block;
}
.toggle_label input:focus + .button_slider {
  box-shadow: 0 0 1px #2196f3;
}
.toggle_label input.checked {
  transition: 0.3s ease-in-out;
}
.toggle_label input.checked + .button_slider {
  background-color: var(--ion-color-primary);
}
.toggle_label input.checked + .button_slider .button_switch_round {
  transform: translateX(21px);
}
.toggle_label input.checked + .button_slider .button_switch_round #theme_svg_icon #sun {
  display: none;
}
.toggle_label input.checked + .button_slider .button_switch_round #theme_svg_icon #moon {
  display: block;
}
.toggle_label .button_slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  border-radius: 28px;
}
.toggle_label .button_slider .button_switch_round {
  display: block;
  position: absolute;
  height: 20px;
  width: 20px;
  left: 4px;
  bottom: 4px;
  transition: 0.4s;
  text-align: center;
  z-index: 3;
  border-radius: 50%;
  overflow: hidden;
  transform-origin: center;
}
.toggle_label .button_slider .button_switch_round #theme_svg_icon #moon {
  display: none;
}

