/* Toggle button custom css*/
.toggle-button input {
  opacity: 0;
}

.toggle-button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
}
.toggle-button .onoff {
  display: -moz-inline-stack;
  display: inline-block;
  vertical-align: middle;
  *vertical-align: auto;
  zoom: 1;
  *display: inline;
  position: relative;
  cursor: pointer;
  width: 73px;
  height: 30px;
  line-height: 30px;
  font-size: 14px;
  margin: 0 !important;
}
.toggle-button .onoff label {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  cursor: pointer;
  background: var(--ion-color-medium-tint);
  border-radius: 25px;
  font-weight: bold;
  color: var(--ion-color-white);
  -webkit-transition: background 0.3s, text-indent 0.3s;
  -moz-transition: background 0.3s, text-indent 0.3s;
  -o-transition: background 0.3s, text-indent 0.3s;
  transition: background 0.3s, text-indent 0.3s;
  text-indent: 27px;
  -webkit-box-shadow: 0px 0px 5px var(--ion-color-dark-shade) inset;
  -moz-box-shadow: 0px 0px 5px var(--ion-color-dark-shade) inset;
  box-shadow: 0px 0px 5px var(--ion-color-dark-shade) inset;
}
.toggle-button .onoff label:after {
  content: "NO";
  display: block;
  position: absolute;
  top: 8px;
  left: 20px;
  width: 100%;
  font-size: 12px;
  color: var(--ion-color-white);
  text-shadow: 0px 1px 0px var(--ion-color-white);
  z-index: 1;
}
.toggle-button .onoff label:before {
  content: "";
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: var(--ion-color-primary);
  position: absolute;
  z-index: 2;
  top: 3px;
  left: 4px;
  display: block;
  transition: left 0.3s;
  box-shadow: 0px 0px 5px var(--ion-color-dark-shade);
  -webkit-transition: left 0.3s;
  -moz-transition: left 0.3s;
  -o-transition: left 0.3s;
  -webkit-box-shadow: 0px 0px 5px var(--ion-color-dark-shade);
  -moz-box-shadow: 0px 0px 5px var(--ion-color-dark-shade);
}
.toggle-button .onoff input:checked + label {
  background: var(--ion-color-dark-shade);
  text-indent: 8px;
}
.toggle-button .onoff input:checked + label:after {
  content: "YES";
  color: var(--ion-color-white);
  left: 0;
}
.toggle-button .onoff input:checked + label:before {
  left: 45px;
}

